import React, { useEffect, useState } from "react";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import { flushSync } from "react-dom";
import AlbumArea from "./AlbumArea";
import Footer from "./Footer";
import ListAlbums from "./ListAlbums";
import Navbar from "./Navbar";
import Searchbar from "./Searchbar";
import Toolbar from "./Toolbar";
import PreviewImage from "./PreviewImage";
import GridControl from "./GridControl";
import Modal from "./Modal";

const App = () => {

    // class for holding information of individual albums
    class Album {
        constructor(title,artists,img_url,id) {
            this.title = title;
            this.artists = artists;
            this.img_url = img_url;
            this.id = id;
        }
    }

    // state of current list of searched albums and the preview image
    const [albumList, setAlbumList] = useState([]);
    const [previewImage, setPreviewImage] = useState();
    const [previewComp, setPreviewComp] = useState();
    const [gridSize, setGridSize] = useState({})
    const [currentAlbums, setCurrentAlbums] = useState([]);
    const [modalText, setText] = useState("");
    const [modalState, setState] = useState(false);

    useEffect(() => {
        if (gridSize !== {} && currentAlbums !== (gridSize.height * gridSize.width)) {
            let oldList = currentAlbums;
        
            let resizedList = [];
            const grid_area = gridSize.height * gridSize.width;
            for (let i = 0; i < grid_area; i++) {
                resizedList.push(null);
                if (i < oldList.length) {
                    resizedList[i] = oldList[i];
                }
            }
            setCurrentAlbums(resizedList);
        }
    }, [gridSize]);

    function calcWidth() {
        let longest_text_size = 0;
        
        for (let i = 0; i < currentAlbums.length; i++) {
            let album_text = currentAlbums[i].title + " - ";
            for (let x = 0; x < currentAlbums[i].artists.length; x++) {
                album_text += "[" + currentAlbums[i].artists[x] + "] "
            }
            if (album_text.length > longest_text_size) {
                longest_text_size = album_text.length;
            }
        }
      
        return longest_text_size * 10;
    }

    function calcSquareLength(height,width) {
        if (height > width) {
            return 1380 / height;
        }
        else
        {
            return 1380 / width;
        }
    }

    async function loadImage(url) {
        return new Promise((resolve, reject) => {
            const img = new Image();
            img.onload = () => resolve(img);
            img.onerror = reject;
            img.crossOrigin = "Anonymous";
            img.src = url;
        });
    }

    async function addImages(ctx,cursor) {
        const square_length = calcSquareLength(gridSize.height,gridSize.width);
      
        for (let i = 0; i < currentAlbums.length; i++) {
            console.log("loading image #" + i)
            await loadImage(currentAlbums[i].img_url)
                .then( image => ctx.drawImage(image,cursor.x,cursor.y,square_length,square_length))
      
            cursor.column_count++;
            cursor.x += square_length;
            console.log(gridSize.width);
            console.log(cursor.column_count);
            if (cursor.column_count == gridSize.width) {
                cursor.x = 10;
                cursor.y += square_length;
                cursor.column_count = 0;
            }

        }

        return ctx;
    }

    function addText(ctx,cursor) {
        cursor.x = 1400;
        cursor.y = 20;
        ctx.font = "16px serif";
        ctx.fillStyle = "white"
      
        currentAlbums.map((album) => {
            let album_text = album.title + " - ";
            for (let i = 0; i < album.artists.length; i++) {
                album_text += "[" + album.artists[i] + "] "
            }
            ctx.fillText(album_text,cursor.x,cursor.y)
            cursor.y += 16;
        })

        return ctx;
    }

    const getAlbumListImage = async () => {
        
        const cursor = {
            x: 10,
            y: 10,
            column_count: 0
        }

        const width = 1400 + calcWidth();

        const canvas = document.createElement("canvas");
        let ctx = canvas.getContext("2d");
        ctx.canvas.width = width;
        ctx.canvas.height = 1400;

        ctx.fillStyle = "black";
        ctx.fillRect(0,0,ctx.canvas.width,ctx.canvas.height)
        ctx = await addImages(ctx,cursor);
        ctx = addText(ctx,cursor);

        const link = document.createElement("a");
        link.href = canvas.toDataURL("image/png");
        link.download = "IMAGE.PNG";
        link.click();
    }

    function showModal(text) {
        console.log("made it here")
        flushSync(() => {
            setText(text);
        });
        console.log(modalText)
        flushSync(() => {
            setState(true);
        });
        console.log(modalState)
    }

    function disableModal() {
        flushSync(() => {
            setState(false);
        });
    }

    function getHelp() {
        showModal("Z");
    }

    const getRecommendations = async () => {
        const { Configuration, OpenAIApi } = require("openai");

        const configuration = new Configuration({
            apiKey: "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",
        });
        const openai = new OpenAIApi(configuration);
        
        const num_of_albums = currentAlbums.length;
        
        let request_string = "Give me a comma seperated list of 10 obscure albums like the following albums with genre names: ";

        for (let i = 0; i < num_of_albums; i++){
            if (i !== 0){
                request_string += " ";
            }
            request_string += currentAlbums[i].title;
            request_string += " by ";
            request_string += currentAlbums[i].artists[0]
            request_string += ";"
        }
        
        console.log(request_string);
        
        openai
            .createChatCompletion({
                model: "gpt-3.5-turbo",
                messages: [{ role: "user", content: request_string}],
            })
            .then((res) => {
                let recommendations = res.data.choices[0].message.content;
                
                console.log(recommendations);
                showModal(recommendations);
            })
            .catch((e) => {
                console.log(e);
            })

    }

    // function that is called whenever an album is searched for in {Searchbar}
    // converts json data from spotify API to formatted object list and sends it to {ListAlbums}
    const handleData = (data) => {
        
        const album_list = data.albums.items.map((item) =>{
            // grabs the title,img url, id, and list of artists from the json object
            const title = item.name;
            const img_url = item.images[0].url;
            const id = item.id;
            let artists = [];
            artists = item.artists.map((artist) => {
                return artist.name;
            })
            // creates and returns the new album object
            const album = new Album(title,artists,img_url,id);
            return album;
        });
        // changes the state of the current albumList to the new album_list
        // this updates {ListAlbums}
        setAlbumList(album_list);
    }

    // function is called whenever an album is selected in {ListAlbums}
    // sets the preview image state to the current selected album in {ListAlbums}
    const updateImage = (album) => {
        setPreviewImage(album);
        const newImageComp = <PreviewImage 
            previewImage={album}
            />
        setPreviewComp(newImageComp);
    }

    const updateGrid = (grid_dimensions) => {
        setGridSize(grid_dimensions)
    }

    const addToCurrentList = (index,album) => {

        let albums = currentAlbums;
        albums[index] = album;
        flushSync(() => {
            setCurrentAlbums(albums);
        })
    }

    return (
        <DndProvider backend={HTML5Backend}>
            <div className="main-page">
                    <div className="workspace">
                        <Navbar />
                        <AlbumArea 
                            gridSize={gridSize} 
                            addToCurrentList={addToCurrentList}
                        />
                        <Footer />
                    </div>
                    <div className="toolside">
                        <h1 className="heading">Album List</h1>
                        <Toolbar getAlbumListImage={getAlbumListImage} getRecommendations={getRecommendations} getHelp={getHelp}/>
                        <GridControl updateGrid={updateGrid}/>
                        <Searchbar handleData={handleData}/>
                        <ListAlbums album_list={albumList} updateImage={updateImage}/>
                        { previewComp }
                    </div>
            </div>
            <Modal modalState={modalState} disableModal={disableModal} modalText={modalText}/>
        </DndProvider>
    );
}
 
export default App;