import { useState } from "react";
import { flushSync } from "react-dom";

const Toolbar = (props) => {
    const [loading1,setLoading1] = useState(false);
    const [loading,setLoading] = useState(false);


    const grabAlbumListImage = () => {
        flushSync(() => {
            setLoading1(true);
        })
        setTimeout(() => {
            setLoading1(false);
        },2000)
        props.getAlbumListImage();
    }

    const getRecommendations = () => {
        flushSync(() => {
            setLoading(true);
        })
        setTimeout(() => {
            setLoading(false);
        },8000)
        props.getRecommendations();
    }

    const getHelp = () => {
        props.getHelp();
    }

    return ( 
        <div className="toolbar">
            <button onClick={grabAlbumListImage}>{ loading1 ? "Loading..." : "Download List" }</button>
            <button onClick={getRecommendations} id="recommend-button">{ loading ? "Loading..." : "Give Me Recommendations"}</button>
            <button onClick={getHelp} id="help-button">?</button>
        </div>
    );
}

export default Toolbar;
