const GridControl = (props) => {
    
    const sendDimensions = () => {
        const height = document.querySelector('.height').value;
        const width = document.querySelector('.width').value;

        const dimensions = {
            height: height,
            width: width
        };

        props.updateGrid(dimensions);
    }
    
    return (
        <form className="control-panel">
            <input className="height" type="number" placeholder="H"/>
            <span>X</span>
            <input className="width" type="number" placeholder="W"/>
            <button 
                onClick={sendDimensions} 
                className="resize" 
                type="button">
                Resize
            </button>
        </form>
    );
}
 
export default GridControl;