import { useEffect, useState } from "react";
import AlbumSquare from "./AlbumSquare";

const AlbumArea = (props) => {
    const [grid,setGrid] = useState([])

    useEffect(() => {
        let new_grid = [];
        const height = props.gridSize.height;
        const width = props.gridSize.width;
        const area = height * width;
        let square_length = 0;

        if (height > width) {
            square_length = 850 / height;
        }
        else
        {
            square_length = 850 / width;
        }

        const grid_area = document.querySelector(".grid-area");
        grid_area.style.gridTemplate = ("repeat(" + height + "," + square_length + "px) / " 
            + "repeat(" + width + "," + square_length + "px)");

        for (let i = 0; i < area; i++)
        {
            const new_square = <AlbumSquare
                addToCurrentList={props.addToCurrentList}
                id={i}
                key={i}
            />
            new_grid.push(new_square);
        }

        setGrid(new_grid);
    },[props.gridSize])

    return (
        <div className="album-area">
            <div className="grid-area">
                {grid}
            </div>
        </div>
    );
}
 
export default AlbumArea;