import { useEffect, useState } from "react";

// component receives album_list and sendUpdate from {App}
const ListAlbums = (props) => {

    const [albums, setAlbums] = useState([]);

    // function called whenever is selected (clicked) in the list
    const sendUpdate = e => {
        // grabs the index of the selected album and sends the object {App}
        const album_index = e.target.getAttribute("data-index");
        const album = props.album_list[album_index];
        props.updateImage(album);
    }

    // called whenever a change is made to the albumList state in {App}
    useEffect(() => {
        // creates a list element for every album in the albumList state
        setAlbums(props.album_list.map((album,i) => {
            // formats the list of artists into a readable string
            let artists = "";
            for (let i = 0; i < album.artists.length; i++) {
                artists += "[" + album.artists[i] + "] ";
            }
            return (
            <li 
                data-index={i}
                className="album-selector" 
                key={i}
                onClick={sendUpdate}>
                {album.title} - {artists}
            </li>
            )    
        }));
    },[props.album_list]);

    return ( 
        <div className="list-albums">
            <ul className="list-holder">
                { albums }
            </ul>
        </div>
    );
}
 
export default ListAlbums;
