function Searchbar({ handleData }) {

    // hold the id,secret, and access token for making requests to the spotify API
    const client_id = 'fcacac8f88d04e6eb2fdf01f970bb574';
    const client_secret = 'e4b36b40a04940c79e794bc48db595c7';
    let access_token = '';

    // replaces every space in the string with %20 (the http code for space)
    function htmlify(string) {
        return string.replace(' ', '%20')
    }

    // function that authenticates the spotify API request with a given id and secret
    async function authenticate(client_id,client_secret) {
        // fetch the access token from the spotify API
        const response = await fetch('https://accounts.spotify.com/api/token', {
            method: "POST",
            body: 'grant_type=client_credentials&client_id=' + client_id + '&client_secret=' + client_secret,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        });
        if (response.status !== 200){
            throw new Error('cannot fetch the data');
        }
        const data = await response.json();
        return data;
    }

    // function that first authenticates the request and then fetches a list of albums
    // the albums are fetched from the spotify API given a query in the search bar and a selected category
    async function retrieveAlbums(search_text,category) {
        // retrieve access token
        await authenticate(client_id,client_secret)
            .then((data) => {
                access_token = data.access_token;
                console.log('Access token expires in: ' + data.expires_in);
            })
            .catch(error => console.log("there has been a problem fetching the data:", error.message));
        
        // create the query string based on the provided category
        let queryString = "";
        search_text = htmlify(search_text);
        if (category === "album") {
           queryString = "q=album:" + search_text;
        }
        else if (category === "artist") {
            queryString = "q=artist:" + search_text;
        }

        // attach query to API url
        const url = 'https://api.spotify.com/v1/search?' + queryString + '&type=album&limit=16'; // limit on albums is 16

        // initiate fetch
        const response = await fetch(url, {
            headers: {
                'Authorization': 'Bearer ' + access_token,
            }
        });
        if (response.status !== 200){
            throw new Error('cannot fetch the data');
        }
        // return the data in json format
        const data = await response.json();
        return data;
    }

    // fetch the albums from the spotify API
    const getAlbums = async () => {
        const search_text = document.querySelector('.search-bar').value;
        const category = document.querySelector('#category').value;
        // query must be longer than 2 characters
        if (search_text.length > 2){
            await retrieveAlbums(search_text,category)
                .then((data) => {
                    handleData(data);
                })
                .catch(error => console.log("there has been a problem fetching the data:", error.message));
        }
        else {
            // some error
        }
    }

    return (  
        <form className ="search-form">
            <input
            autoComplete="off"
            placeholder="Search for an Album"
            type="search" 
            name="search" 
            className="search-bar"
            />
            <select name="category" id="category">
                <option value="album">Album</option>
                <option value="artist">Artist</option>
            </select>
            <button onClick={getAlbums} type="button" id="search-button">Go</button>
        </form>
    );
}
 
export default Searchbar;