import { useState } from "react";
import { useDrop } from "react-dnd";

function AlbumSquare(props) {

    const [currentImage,setCurrentImage] = useState();

    const [{ isOver },drop] = useDrop(
        () => ({
            accept: "ALBUM",
            drop: (item) => {
                setImage(item.album);
            },
            collect: (monitor) => ({
                isOver: !!monitor.isOver()
            })
        }),
    )

   const setImage = (album) => {
        const newImage = <img 
            className="active-album"
            src={album.img_url} 
            alt="An added album" 
            />;
        setCurrentImage(newImage);
        props.addToCurrentList(props.id,album);
    }

    return(
        <>
            <div
                id={props.id}
                key={props.id}
                ref={drop} 
                className="album-square"
                style={{
                    position: 'relative',
                }}
            >
                {isOver && (
                    <div
                    style={{
                        height: '100%',
                        width: '100%',
                        position: 'absolute',
                        zIndex: 2,
                        opacity: 0.5,
                        backgroundColor: 'white',
                        }}
                    />
                )}
                { currentImage }
            </div>
        </>
    );
}

export default AlbumSquare