import React from "react";

export default class Modal extends React.Component {
    render() {
        console.log(this.props.modalState)
        console.log(this.props.modalText)
        if(!this.props.modalState){
            console.log("modal did nor render");
            return null;
        }
        let headerText;
        let contentText;
        if(this.props.modalText[0] == '1'){
            headerText = "Your Recommendations"
            contentText = <pre className="modal-text">{this.props.modalText}</pre>
        }
        else{
            headerText = "Help"
            contentText =   <div className="help-text">
                                <ul>
                                    <li>Welcome to the Album List Web App! To make your first list, follow these instructions: </li>
                                    <li>1. Select the dimension of the grid you wish to create.</li>
                                    <li>2. Search for the album you wish to add to the grid by typing the the search bar.</li>
                                    <li>3. Click on the name of the album you wish to add in the box below the searchbar.</li>
                                    <li>4. Drag the album cover at the bottom of the page onto the grid.</li>
                                    <li>5. Download the list by clicking the download button at the top of the page.</li>
                                    <li>6. You can recieve album recommendations based on your current list by clicking the recommendation button </li>
                                </ul>
                            </div> 
        }
        return (
            <div className="modal">
                <div className="modal-content">
                    <div className="modal-header">{headerText}
                        <button className="disable-modal" onClick={this.props.disableModal}>&times;</button>
                    </div>
                    {contentText}
                </div>
            </div>
        );
    }
}